/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #ffffff;
  font-family: "Exo";
  font-weight: 100;
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both; }

/* Copy */
p {
  margin-bottom: 1.5rem;
  color: #ffffff; }
  @media (min-width: 1040px) {
    p {
      font-size: 30px; } }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #242323;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid;
  border-color: #00cc00;
  border-radius: 3px;
  background: transparent;
  color: #00cc00;
  font-size: .75rem;
  line-height: 1;
  padding: 1rem 3rem; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #666;
  border-radius: 3px; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #111; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }
  @media (min-width: 600px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"] {
      padding: 7px; } }
  @media (min-width: 1040px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"] {
      padding: 10px; } }

textarea {
  padding-left: 3px;
  width: 100%; }

#submit-quote input {
  border: 2px solid;
  border-color: #00cc00;
  border-radius: 3px;
  background: transparent;
  color: #00cc00;
  font-size: .75rem;
  line-height: 1;
  padding: 10px 25px; }
  @media (min-width: 600px) {
    #submit-quote input {
      padding: 10px 60px; } }
  @media (min-width: 1040px) {
    #submit-quote input {
      margin: 0 20%; } }

@media (min-width: 1040px) {
  #submit-quote .submit {
    margin-top: 2%; } }

#submit-quote #quote {
  padding: 50px 70px; }
  @media (min-width: 600px) {
    #submit-quote #quote {
      padding: 100px 220px; } }
  @media (min-width: 1040px) {
    #submit-quote #quote {
      padding: 50px 220px;
      margin: 0 20%; } }

fieldset {
  border: none;
  display: flex;
  flex-direction: row; }
  fieldset label {
    width: 40%; }
  fieldset input {
    border: 2px solid;
    border-color: #00cc00;
    border-radius: 3px;
    background: transparent;
    color: #00cc00; }
  fieldset .search-submit {
    border: none;
    margin: 0;
    padding: 0; }
    fieldset .search-submit:before {
      font-family: 'FontAwesome';
      content: '\f002'; }
      @media (min-width: 600px) {
        fieldset .search-submit:before {
          font-size: 20px; } }
      @media (min-width: 600px) {
        fieldset .search-submit:before {
          font-size: 25px; } }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.site-info {
  margin: 4.5rem 0 3rem; }

.main-navigation {
  clear: both;
  display: block;
  width: 100%; }
  .main-navigation ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
    display: flex; }
    @media (min-width: 1040px) {
      .main-navigation ul {
        justify-content: flex-end;
        margin-right: 5%; } }
  .main-navigation li {
    position: relative;
    margin: 0 2%; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%; }

.qodlogo {
  padding-top: 5%;
  display: flex;
  justify-content: center; }
  @media (min-width: 600px) {
    .qodlogo {
      justify-content: flex-start;
      margin: 0 2%; } }
  @media (min-width: 1040px) {
    .qodlogo {
      justify-content: center; } }
  .qodlogo img {
    width: 300px; }
    @media (min-width: 600px) {
      .qodlogo img {
        width: 400px; } }
    @media (min-width: 1040px) {
      .qodlogo img {
        width: 750px; } }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #00cc00;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
body {
  background-color: #242323; }

/* Home Page */
@media (min-width: 1040px) {
  .home main {
